.header {
  box-sizing: border-box;
  height: 100vh;
  padding: $headerTopPadding 0 0;

  @include onLaptop {
    padding: $headerTopPadding*2 0 0;
  }

  background-image: url(../images/photos/header.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__title {
    margin-top: 120px;
    font-size: 40px;
    line-height: 100%;
    font-weight: 700;
    font-style: normal;
    color: #fff;
    text-align: center;
    letter-spacing: -1.5px;

    @include onLaptop() {
      font-weight: 700;
      font-size: 64px;
      line-height: 64px;
    }

    @include onDesktop() {
      font-weight: 700;
      font-size: 80px;
      line-height: 80px;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
  }

  &__top {
    @include top-actions;
  }

